* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 100vh;
}

.navbar {
  background-color: aqua !important;
}

.reg-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  border: 20px;
}

.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 6px gray;
}

.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}

.nik {
  background-color: blanchedalmond;
  background-image: linear-gradient(to right, yellow, lightblue, lightgreen, orange);
}

.hil {
  background-image: linear-gradient(to bottom right, lightgreen, yellow, gray);
  height: 100%;
}

.login-page {
  position: relative;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  /* Adjust to your needs */
  opacity: 0.9;
  /* Adjust the opacity as needed */
  z-index: -1;
  /* Place the image behind other content */
}

.hd {
  margin-top: 60px;
  align-items: center;
}
.filter-tab .ant-select-selector {
  width: 150px !important;
}